<template>
	<div>
		<div class="back-title"><span>最新评论</span></div>
		<div class="commentlist">
			<ul>
				<li class="clearfix"  v-for="item in commentlist">
					<div class="comment-user">
						<a href="/">
							<Avatar size="large" v-bind:src="item.user.avatar_url" style="background-color: #87d068" icon="person"></Avatar>
						</a>
					</div>
					<div class="comment-info">
						<div class="user-name">{{item.user.screen_name}}<span>{{item.create_time}}</span></div>
						<div class="comment-txt">
							{{item.content}}
						</div>
						<div class="comment-article">
							评论了我的文章：<a v-bind:href="item.group.url" target="_blank">「{{item.group.title}}」</a>
						</div>
					</div>
				</li>
			</ul>
		</div>
		<div class="loadmore">
    		<Button type="text"  @click="toLoading" v-if="spinShowloadmore">加载更多数据</Button>
    		<Spin fix  v-if="spinShow"><Icon type="load-c" size=12 class="demo-spin-icon-load" style="padding: 0 5px;"></Icon>数据加载中...</Spin>
    	</div>
	</div>
</template>

<script>
export default {
  data () {
    return {
      spinShow: false,
      spinShowloadmore: true,
      commentlist: [],
      page: 1
    }
  },
  methods: {
    toLoading () {
      this.spinShow = true
      this.spinShowloadmore = false
      this.getlist()
    },
    getlist () {
      this.spinShow = true
      this.$http.get('/static/js/comment.json?page=' + this.page, {page: this.page}, {emulateJSON: true}).then(
         function (res) {
           for (let i = 0; i <= res.data.data.length - 1; i++) {
             this.commentlist.push(res.data.data[i])
           }
           this.page++
           this.spinShow = false
           this.spinShowloadmore = true
         }, function (res) {
      }
     )
    }
  },
  created: function () {
    this.getlist()
  }
}
</script>
<style>
</style>


